.menu {
  box-sizing: border-box;
  height: 100vh;

  font-weight: 600;
  color: #1d1d1d;

  text-transform: uppercase;

  background-color: $menu-background-color;

  overflow: auto; /* to scroll the menu if needed */

  &__top {
    position: sticky; /* to keep cross always visible */
    top: 0;
    z-index: 1;

    margin-bottom: 24px;

    @include on-tablet {
      margin-bottom: 0;
    }
  }

  &__nav {
    margin-bottom: 40px;
  }

  &__phone-number {
    display: block;
    margin-bottom: 16px;

    color: inherit;
    text-decoration: none;
    font-size: 16px;
    line-height: 27px;
  }

  &__book-a-test-ride {
    display: block;
    width: fit-content;

    padding-bottom: 7px;
    border-bottom: 1px solid #1d1d1d;

    color: inherit;
    text-decoration: none;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
  }
}
